import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			tabCurrentIndex: 0,
			totalPages: 1,
			currentpage: 1,
			refresh: true,
			reflash: false,
			orderList: [],
			goodsList:[],
			navList: [{
					state: 0,
					text: '一级分销',
					orderList: [],
					num: '0'
				},
				{
					state: 1,
					text: '二级分销',
					orderList: [],
					num: '0'
				},
				{
					state: 2,
					text: '三级分销',
					orderList: [],
					num: '0'
				}
			]
		}
	},
	onLoad(){
		// this.tabCurrentIndex = options.state;
		this.loadData()
	},
	onPullDownRefresh() {
		this.currentpage = 1
		this.refresh = true;
		this.tabCurrentIndex = 0;
		this.loadData(true);
	},
	onReachBottom() {
		if(this.currentpage >= this.totalPages) return false
		this.currentpage++;
		this.loadData()
	},
	methods: { 
		navTo(id) {
			uni.navigateTo({
				url: `/pages/order/orderDetail?id=${id}`
			})
		},
		//获取订单列表
		loadData(flag){
			uni.showLoading({title: '正在加载'})
			let userId = uni.getStorageSync('userId')
			let orderurl = '/retailUser/getSubRetailUserlist'
			api.httppostfx(orderurl,{retailUserId: userId}).then((res) => {
				let respons = res.data
				if (respons.code == 200) {
					if(this.refresh) this.orderList = []
					this.orderList = respons.data;
					this.goodsList = respons.data.firstRetailUserList;
					this.navList[0].num = this.orderList.firstRetailUserList.length;
					this.navList[1].num = this.orderList.secondRetailUserList.length;
					this.navList[2].num = this.orderList.thirdRetailUserList.length;
					this.totalPages = respons.data.pages;
					//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
					// this.$set(navItem, 'loaded', true);
					//判断是否还有数据， 有改为 more， 没有改为noMore 
					// navItem.loadingType = 'more';
					this.refresh = false
				} else {
					uni.showToast({title: respons.errmsg, icon: 'none'})
				}
				if(flag) uni.stopPullDownRefresh()
				uni.hideLoading()
			})
		}, 
		//顶部tab点击
		tabClick(index){
			this.refresh = true
			this.tabCurrentIndex = index;
			this.currentpage = 1;
			switch (index) {
				case 0: this.goodsList = this.orderList.firstRetailUserList;break;
				case 1: this.goodsList = this.orderList.secondRetailUserList;break;
				case 2: this.goodsList = this.orderList.thirdRetailUserList;break;
			}
			// this.loadData()
		},
		//删除订单
		deleteOrder(index){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				this.orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		//取消订单
		cancelOrder(item){
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(()=>{
				let {stateTip, stateTipColor} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip, 
					stateTipColor
				})
				
				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val=>val.id === item.id);
				index !== -1 && list.splice(index, 1);
				
				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
}
